.cameraPage{
    background-color: #ADADAD;
    .videoBox{
        display: flex;
    }
    video{
        background: #000;
        margin: auto;
        width: 100vw;
    }
    .iconBtn{
        border: none;
        padding: 0;
        background: none;
    }
    .captureBtn{
        position: fixed;
        left: 50%;
        bottom: 24px;
        width: 75px;
        height: 75px;
        transform: translateX(-50%);
        background-size: 100%;
    }
    .cameraCloseBtn{
        position: fixed;
        top: 20px;
        right: 37px;
    }
    .cameraBox{
        position: fixed;
        top: 67px;
        left: 0;
        width: 100%;
    }
    .videoContainer{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba($color: #ADADAD, $alpha: 1);
    }
    .videoBox{
        position: relative;
        width: 302px;
        height: 390px;
        background: #000;
        .downloadBtn{
            position: absolute;
            left: 50%;
            bottom: 0;
            width: 50px;
            height: 50px;
            transform: translate(-50%,50%);
        }
    }
}